import React, {useEffect, useState} from 'react';
import getDateTime from '../assets/js/updateTime';

function Topbar() {

    const [localTime, setLocalTime] = useState (getDateTime);
    const topbarStyle = {opacity: "0.75", backgroundColor: "#1a1a1a"}

    const timer = () => setLocalTime (getDateTime())
    useEffect (() => {setInterval (timer, 1000)}, [])

    /* new Date().toLocalTimeString() */

    return (
        <div>
            {/* <!-- Topbar --> */}
            <nav className="navbar navbar-expand navbar-light bg-white topbar mb-4 mt-4 static-top shadow">

                <div className="d-block fixed-top py-1" style={topbarStyle}>
                    <p className="text-white font-weight-bold small float-right mx-2 my-0 pr-2">{localTime}</p>
                </div>

            </nav>
            {/* <!-- End of Topbar --> */}
        </div>
    )
}

export default Topbar;